import wPicker from "@/components/w-picker/w-picker.vue";

var wxCharts = require("../../../utils/wxcharts.js");
var _self;
var Charts;

var Data = {
	categories: ['2019/4', '2019/5', '2019/6'],
	series: [{
			name: 'AQI',
			data: [396, 423, 418]
		}
	]
};
var width;
export default {
	components:{
		wPicker
	},
	
	onLoad: function() {
		uni.setNavigationBarTitle({
			title: this.getNowQuarter()+'市空气质量'
		});
		
		uni.getSystemInfo({
			success(res) {
				width = res.screenWidth - 10;
			}
		})
	},
	data() {
		return {
           title: 'Hello',
           sdate: this.getNowQuarter(),
           tabList:[{
           	mode:"yearQuarter",
           	name:"年季",
           	value:[this.getNowYear(),0] //年月在列表的序号
           }],
           tabIndex:0
		}
	},
	computed:{
		mode(){
			return this.tabList[this.tabIndex].mode
		},
		defaultVal(){
			return this.tabList[this.tabIndex].value
		}
	},
	onReady: function() {
		this.ShowCharts("charts", Data);
		//this.hideLoading();
	},
	methods: {
		toggleTab(index){
			this.tabIndex=index;
			this.$refs.picker.show();
		},
		onConfirm(val){
			//当前所选择的日期
			this.sdate = val.result;
			console.log(val.result);
			
			uni.setNavigationBarTitle({
			title: val.result+'市空气质量'
		   });
		},
		/*显示图表*/
		ShowCharts: function(canvasId, data) {
			Charts = new wxCharts({
				canvasId: canvasId,
				type: 'line',
				legend: true,
				fontSize: 11,
				background: '#FFFFFF',
				animation: true,
				categories: data.categories,
				series: data.series,
				width: width,
				height: 280,
				pixelRatio:1,
			});
		},
		
		goDetail:function(id,storeName){			
			let detail = {
					id: id,
					storeName:storeName,
					date:this.sdate
				}
				uni.navigateTo({
					url: "daydata02?detail=" + encodeURIComponent(JSON.stringify(detail))
				})
		},
		
		getNowQuarter: function() {
			var date = new Date();
			var year = date.getFullYear();
			var month = date.getMonth() + 1;
			var quarter = "";
			if(month<=3){
				quarter="第一季度";
			}else if(month<=6){
				quarter="第二季度";
			}else if(month<=9){
				quarter="第三季度";
			}else if(month<=12){
				quarter="第四季度";
			}
			
			var currentdate = year + ' ' + quarter ;
			return currentdate;
		},
		
		getNowYear: function() {
			var date = new Date();
			var year = date.getFullYear();
			var currentdate = year -2018 ;
			return currentdate;
		},
		
	}
}
